	module.exports = {"data":{"site":{"siteMetadata":{"title":":-)","author":"github.com/9renpoto"}},"markdownRemark":{"id":"/home/travis/build/9renpoto/entry/src/pages/2016-09-29-reactive-shinjuku-03/index.md absPath of file >>> MarkdownRemark","html":"<h1>参加メモ</h1>\n<p><a href=\"https://reactive-shinjuku.connpass.com/event/38411/\">https://reactive-shinjuku.connpass.com/event/38411/</a></p>\n<p>ハッシュタグ <code>#reactive_shinjuku</code></p>\n<h2>リアクティブDDDについて</h2>\n<p>株式会社ChatWork加藤潤一氏（@j5ik2o）</p>\n<ul>\n<li>\n<p>リアクティブDDD</p>\n<ul>\n<li>リアクティブシステム &#x26;&#x26; ドメイン駆動設計</li>\n</ul>\n</li>\n<li>ActorはDDDで示されるコンセプトを表現できる</li>\n<li>リアクティブシステムとAkka</li>\n<li>\n<p>リアクティブ宣言 <a href=\"https://www.reactivemanifesto.org/\">https://www.reactivemanifesto.org/</a></p>\n<ul>\n<li>Elastic</li>\n<li>Responsive</li>\n<li>Reslilient</li>\n<li>Message Driven</li>\n</ul>\n</li>\n<li>\n<p>ソフトウェアの災いを表す用語</p>\n<ul>\n<li>誤差・誤り（error)</li>\n<li>欠陥（defect, バグを含む概念）</li>\n<li>障害（fault)</li>\n<li>故障（failure)</li>\n</ul>\n</li>\n<li>メッセージ駆動</li>\n<li>\n<p>Akka</p>\n<ul>\n<li>2011年の記事ではErlangの2倍のスループットを発揮した</li>\n</ul>\n</li>\n<li>\n<p>ドメインモデル</p>\n<ul>\n<li><code>モデル = 考え方とコードを結びつけたソフトウェア</code> を実現する</li>\n</ul>\n</li>\n<li>ドメイン層の隔離</li>\n</ul>\n<p>ここからスライドを参照してあとで勉強する。</p>\n<p><a href=\"https://speakerdeck.com/yoskhdia/modeling-the-domain-event\">https://speakerdeck.com/yoskhdia/modeling-the-domain-event</a></p>\n<h2>Lagomで学ぶReactive Microservices Architecture</h2>\n<p>株式会社TIS根来和輝（@negokaz）</p>\n<ul>\n<li>Reactive Microservices Architecture</li>\n<li>\n<p>Lagom</p>\n<ul>\n<li>非同期・ノンブロッキングAPI</li>\n<li>Shardingによるステートフルアーキテクト</li>\n<li>分散型の永続化機構</li>\n<li>CQRS + Event Sroucing</li>\n<li>Circuit Breaker</li>\n</ul>\n</li>\n</ul>\n<p>www.slideshare.net/negokaz/lagom-reactive-microservices-architecture</p>","frontmatter":{"title":"第3回 Reactive System Meetup in 西新宿","date":"September 29, 2016"}}},"pathContext":{"path":"/entry/2016/09/29/reactive-shinjuku/"}}

